.boton{
    height: 75px;
    display: flex;
    flex: 1 1;
    justify-content: center;
    align-items: center;
    font-size: 38px;
    background-color: #1b1b32;
    color: white;
    border-radius: 25px;
    border: 2px solid white;
    margin: 4px;
    cursor: pointer;
    user-select: none;
}

.boton:hover{
    background-color: purple;
}

.operador{
    background-color: rgb(41, 126, 97);
}

.operador:hover{
    background-color: orange;
}